.card.food-card{
    box-shadow: 0px 3px 6px #00000029;
    opacity: 1;
    
    cursor: pointer;

}
.card.food-card:hover{
    background: rgb(176, 213, 226) 0% 0% no-repeat padding-box;
    opacity: 1;
    
   }

.card-header{
   padding: 20px 0px;
    font: normal normal bold 20px/10px Poppins;
    letter-spacing: 0px;
    color: #192F60;
    

}


.card-body.food-card-body{
    background-color:#eef0f3 ;
   
    

}
.card-material{
    
   
    text-align: center;
}

.card-description{
    
    font: normal normal italic 15px/10px Poppins;
    letter-spacing: 0px;
    color: #8C96AB;
    
    

}

.card-price{
    font: normal normal bold 14px/21px Poppins;
    color: #2b4988;

}
.card-restaurent{
    font: normal normal normal 14px/21px Poppins;
    color: #3e5fa8;
}

