}

  #signup{
    text-decoration: none;
    background:transparent;
    color: white;
    padding: .4rem 1rem;
    font-size: 1rem;
    margin-left: .7rem;
    border: 1px solid white;
    
}
  .card-body{
    position: relative;
    
    z-index: 2;
  }
  
  
  .logo{
    
    width: 150px;
    height: 150px;
    color: red;
    font-size: 90px;
    border-radius: 50%;
    background-color: white;
    text-align: center;
    margin: 0 auto;
    
}
.main-heading{
  position: relative;
  color: white;
  font: normal normal 600 30px/50px Poppins;
  padding-top: 20px; 
  margin: 0 auto;
  max-width: 640px;
  z-index: 2;
  text-align: center;
}

.form-group2{
  display: flex;
  flex-direction: row;
  max-width: 640px;
  margin: 1rem auto;
  padding-bottom: 50px;
}

.location{
    padding:10px 20px;
    width: 35%;
    height: 60px;
    font: normal normal normal 15px/23px Poppins;
    letter-spacing: 0px;
    color: #636F88;
    opacity: 1;
}

.resturant{
  width: 60%;
  height: 60px;
  margin-left: 20px;
} 
.resturant i {
  position: absolute;
  padding-left: 20px;
  padding-top: 15px;
  padding-right: 0px;
  color: #636F88;
}
.Field {
  width: 100%;
  padding-top: 15px;
  padding-bottom:15px;
  padding-left: 40px;
  font: normal normal normal 15px/23px Poppins;
  letter-spacing: 0px;
  color: #636F88;
  opacity: 1;
}
.qs-heading1{
  margin-top: 50px;
  font: normal normal bold 30px/20px Poppins;
  letter-spacing: 0px;
  color: #192F60;
}
.qs-text1{
  margin-top: 20px;
  margin-bottom: 20px;
  font: normal normal normal 18px/20px Poppins;
  letter-spacing: 0px;
  color: #8C96AB;
}
.qs{
  padding-top: 20px;
  text-align: left;
  margin-bottom: 30px !important;
}


.card-horizontal {
  display: flex;
  flex: 1 1 auto;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  opacity: 1;
  text-align: left;
  cursor: pointer;
  
}
.card-horizontal:hover{
 background: skyblue 0% 0% no-repeat padding-box;
 opacity: .8;
 
}
.qs-image{
  width:80%;
  height:100%;
}

.qs-col h3{
  font: normal normal 600 18px/27px Poppins;
  color: #192F60;
}
.qs-col p{
  font: normal normal normal 14px/21px Poppins;
  color: #8C96AB;
}
@media only screen and (max-width: 770px){
  .form-group{
    display: flex;
    flex-direction: column;
    max-width: 640px;
    margin: 1rem auto;
    padding-bottom: 20px;
  }
  .location{
    width: 100%;
    margin-bottom: 20px;
  }
  .resturant{
    width: 100%;
    margin-left: 0px;
  }
}
@media only screen and (max-width:350px){
   .story-page{
    width:350px !important;
    margin: 0;
   }
}


 
 